import {
  Wrapper
} from "../../components";
import Pacman from 'react-pacman';
import { useRef } from "react";
import './Pacman.scss';

const PacmanGame = () => {
  const pacmanRef = useRef()

  return (
    <Wrapper>
      <div className='pacman-section'>
           <div className='pacman-game'>
           <div style={{display: 'flex', width: '100%', justifyContent: 'center', paddingTop: '200px'}}>
           <Pacman gridSize={12} ref={pacmanRef}/>
           </div>
           </div>
           </div>
    </Wrapper>
  );
};

export default PacmanGame;
