import {
  BackendError,
  NotFound,
  Signup
} from "./pages";
import './index.css';
import { BrowserRouter, Switch } from "react-router-dom";

import AppProvider from "./components/AppProvider/AppProvider";
import DashboardColorBoxer from "./containers/DashboardColorBoxer";
import DashboardColorPicker from "./containers/DashboardColorPicker";
import DashboardMatch from "./containers/DashboardMatch";
import DashboardFixit from "./containers/DashboardFixit";
import DashboardFindAmount from "./containers/DashboardFindAmount";
import DashboardLabel from "./containers/DashboardLabel";
import DashboardPacman from "./containers/DashboardPacman";
import DashboardPuzzle from "./containers/DashboardPuzzle";
import DashboardWheel from "./containers/DashboardWheel";
import DashboardWordSearch from "./containers/DashboardWordSearch";
import DashboardSelect from "./containers/DashboardSelect";
import DashboardCreate from "./containers/DashboardCreate";
import Dashboard from "./containers/Dashboard";
import React from "react";
import { render } from "react-dom";
import DashboardTest from "./containers/DashboardTest";
import Confirm from "./pages/Confirm/Confirm";
import { Provider } from "./Context/AuthContext";
import Public from './pages/Routes/Public';
import Private from './pages/Routes/Private';

render(
  <AppProvider>
    <BrowserRouter basename={process.env.PUBLIC_URL}>
    <Provider>
      <Switch> 
        <Private path="/" component={Dashboard} exact />
      <Private path="/games/test" component={DashboardTest} exact />
        <Private path="/games/colorboxer" component={DashboardColorBoxer} exact />
        <Private path="/games/colorpicker" component={DashboardColorPicker} exact />
        <Private path="/games/match" component={DashboardMatch} exact />
        <Private path="/games/fixit" component={DashboardFixit} exact />
        <Private path="/games/findamount" component={DashboardFindAmount} exact />
        <Private path="/games/label" component={DashboardLabel} exact />
        <Private path="/games/pacman" component={DashboardPacman} exact />
        <Private path="/games/puzzle" component={DashboardPuzzle} exact />
        <Private path="/games/wheel" component={DashboardWheel} exact />
        <Private path="/games/wordsearch" component={DashboardWordSearch} exact />
        <Private path="/confirm" component={Confirm} exact />
        <Private path="/games/select" component={DashboardSelect} exact/>
        <Private path="/create" component={DashboardCreate} exact/>
        <Public path="/signup" component={Signup} exact/>
        <Public path="/404" component={NotFound} exact/>
      </Switch>
    </Provider>
    </BrowserRouter>
  </AppProvider>,
  document.getElementById("root")
);
