import {
  Wrapper
} from "../../components";
import React from "react";
import GameData from "../../GameData/GameData";
import './CreateGame.scss';
import { NavLink } from "react-router-dom";

const CreateGame = () => {

  return (
    <Wrapper>
      <div className="create-game-big-background-section">
       <div style={{display: 'flex', justifyContent: 'center', alignItems: 'center', width: '100%', minHeight: '90vh'}}>
       <ul className="create-game-list">
          {
            GameData.map((elem, index) => (
                <li className="create-game-item" style={{listStyleType: "none"}} key={index}>
                    <NavLink style={{textDecoration: 'none', transition: '.5s ease all'}} to={'/games' + elem.link}>
                    <h1>{elem.name}</h1>
                </NavLink>
                </li>
            ))
          }
       </ul>
       </div>
       </div>
    </Wrapper>
  );
};

export default CreateGame;
