import {
  Wrapper
} from "../../components";
import WheelComponent from "./WinWheel";
import "./Wheel.css";
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';
import { useState, useRef } from "react";

const Wheel = () => {
  const [open, setOpen] = useState(false);
  const [title, setTitle] = useState('');
  const [text, setText] = useState('');
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  const [openFirst, setOpenFirst] = useState(false);
    const handleOpenFirst = () => setOpenFirst(true);
    const handleCloseFirst = () => setOpenFirst(false);
  let modalRef = useRef();


  const segments = [
    { isSuper: false, value: "100$" },
    { isSuper: true, value: "1000$" },
    { isSuper: false, value: "400$" },
    { isSuper: false, value: "800$" },
    { isSuper: true, value: "7000$" },
    { isSuper: true, value: "5000$" },
    { isSuper: false, value: "300$" },
    { isSuper: true, value: "2000$" }
  ];
  const segColors = [
    "#2FAF76",
    "#07cdae",
    "#C93E77",
    "#FAA62A",
    "#F2F3EB",
    "#2DAA72",
    "#E55052",
    "#FAA62A"
  ];
const onFinished = (winner) => {
  setTitle(winner.isSuper == true ? 'SUPER GIFT' : 'MINI GIFT');
  setText(winner.isSuper == true ? `Your SUPER GIFT ${winner.value}` : `Your MINI GIFT ${winner.value}`);
  handleOpen()
};

let answer = 
[
  {id: 2, answers: "11", isCorrect: false},
  {id: 3, answers: "15", isCorrect: false},
  {id: 1, answers: "13", isCorrect: true},
  {id: 4, answers: "16", isCorrect: false}
]

let quest = { 
  questions: "how old are you ?"
}

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
};
const styleError = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
  pt: 6,
  pb: 2
};

  return (
    <Wrapper>
      <div className="wheel-bg-section">
      <div className="Wheel-Section">
        <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <Typography id="modal-modal-title" variant="h6" component="h2">
            {title}
          </Typography>
          <Typography id="modal-modal-description" sx={{ mt: 2 }}>
            {text}
          </Typography>
        </Box>
      </Modal>
      <Modal
        open={openFirst}
        onClose={handleCloseFirst}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
        >
        <Box sx={styleError}>
        <div className="circle-wrapper">
    <div className="error circle"></div>
    <div className="error-icon">
      <i className="fa fa-times"></i>
    </div>
  </div>
          <Typography id="modal-modal-title" variant="h6" component="h2">
            Level failed
          </Typography>
          <Typography id="modal-modal-description" sx={{ mt: 2 }}>
            You failed level
          </Typography>
        </Box>
      </Modal>
      <div style={{display: 'flex', width: '100%', justifyContent: 'center', minHeight: '90vh', alignItems: 'center'}}>
      <div>
        <p className="quest-text">{quest.questions}</p>
      <WheelComponent
        segments={segments}
        segColors={segColors}
        winningSegment={segments[3]}
        onFinished={(winner) => onFinished(winner)}
        primaryColor="#4C2DC6"
        contrastColor="#4D4C7D"
        buttonText="Spin"
        isOnlyOnce={false}
        size={200}
        upDuration={300}  
        downDuration={300}
        fontFamily="Open Sans"
      />
      <div className="start-game-wheel" style={{display: "block"}}>
      <div style={{display: "flex", flexWrap: "wrap", width: "300px", justifyContent: "space-around"}}>
      {
        answer.map((elem, index) => (
          <button style={{marginBottom: "10px", opacity: "1"}} id="click-btn" key={index} value={elem.answers} data-id={elem.id} type="button" className="action-button animate blue welcome" onClick={(e) => {
            let findAnswer = answer.find(element => element.id == e.target.dataset.id);
            
            if(findAnswer.isCorrect == true) {
              window.localStorage.setItem('failed', false)
              setOpenFirst(false)
            }else {
              window.localStorage.setItem('failed', true)
              setOpenFirst(true)
            }
             
          }}>{elem.answers}</button>
        ))
      }
      </div>
        <button onClick={() => {
          window.location.reload()
      }} className="action-button animate blue welcome" style={{marginLeft: "27px"}}>Restart Game</button></div>
      </div>
      </div>
    </div>
    </div>
    </Wrapper>
  );
};

export default Wheel;
