import {
  Wrapper
} from "../../components";
import { useState } from "react";
import './ColorPicker.scss'

const ColorPicker = () => {
  const [color, setColor] = useState('#fff')

  return (
    <Wrapper>
     <div className="color-picker-section">
     <div className='color-section'>
         <div>
         <div style={{display: 'flex', justifyContent: 'space-between', flexWrap: 'wrap', width: '800px'}}>
         <div>
         <i style={{color: '#fff', fontSize: '150px', marginBottom: '30px'}} className="fa-solid fa-shirt" onClick={(e) => {
            e.target.style.color = color;
         }}></i>
         </div>
         <div>
         <i style={{color: '#fff', fontSize: '150px', marginBottom: '30px'}} className="fa-solid fa-crown" onClick={(e) => {
            e.target.style.color = color;
         }}></i>
         </div>
         <div>
         <i style={{color: '#fff', fontSize: '150px', marginBottom: '30px'}} className="fa-solid fa-briefcase" onClick={(e) => {
            e.target.style.color = color;
         }}></i>
         </div>
         <div>
         <i style={{color: '#fff', fontSize: '150px', marginBottom: '30px'}} className="fa-solid fa-crow" onClick={(e) => {
            e.target.style.color = color;
         }}></i>
         </div>
         </div>
         <div style={{display: 'flex', justifyContent: 'space-around', width: '99%'}}>
         <div id="swatch">
  <input type="color" id="color" name="color" defaultValue="#FF0000" onChange={(e) => setColor(e.target.value)}/>
  <div className="info">
    <h1>Input</h1>
    <h2>Color Change</h2>
  </div>
         </div>
         </div>
         </div>
         </div>
     </div>
    </Wrapper>
  );
};

export default ColorPicker;
