import {
  Wrapper
} from "../../components";
import { useState } from "react";
import './Select.css';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';


const Select = () => {
 
  const [open, setOpen] = useState(false);
    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);
    const [openFirst, setOpenFirst] = useState(false);
    const handleOpenFirst = () => setOpenFirst(true);
    const handleCloseFirst = () => setOpenFirst(false);

    let answer = [
        {
            id: 1,
            answers: "lion",
            isCorrent: false
        },
        {
            id: 2,
            answers: "tiger",
            isCorrent: true
        }
    ]
    
    let quest = {
      questions: "what an animal it is",
      questPoster: "https://i.pinimg.com/originals/4b/87/9f/4b879f200301af23d0e107b74f4d6bec.jpg"
    }
    const style = {
      position: 'absolute',
      top: '50%',
      left: '50%',
      transform: 'translate(-50%, -50%)',
      width: 400,
      bgcolor: 'background.paper',
      border: '2px solid #000',
      boxShadow: 24,
      paddingTop: 8,
      paddingBottom: 5,
      paddingLeft: 5,
      paddingRight: 5,
    };

  return (
    <Wrapper>
      <div className="select-bg-section">
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
        >
        <Box sx={style}>
  <div className="circle-wrapper">
    <div className="success circle"></div>
    <div className="icon">
      <i className="fa fa-check"></i>
    </div>
  </div>
          <Typography id="modal-modal-title" variant="h6" component="h2">
            Level success
          </Typography>
          <Typography id="modal-modal-description" sx={{ mt: 2 }}>
            You succeeded level
          </Typography>
        </Box>
      </Modal>
      <Modal
        open={openFirst}
        onClose={handleCloseFirst}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
        >
        <Box sx={style}>
        <div className="circle-wrapper">
    <div className="error circle"></div>
    <div className="error-icon">
      <i className="fa fa-times"></i>
    </div>
  </div>
          <Typography id="modal-modal-title" variant="h6" component="h2">
            Level failed
          </Typography>
          <Typography id="modal-modal-description" sx={{ mt: 2 }}>
            You failed level
          </Typography>
        </Box>
      </Modal>
         <div className='big-select-section'>
         <div>
         <div>
         <img style={{width: "500px", borderRadius: "10px", border: "3px solid #fff"}} src={quest.questPoster} alt="img"/>
         <h1 className='big-select-title'>{quest.questions}</h1>
         </div>
         <div style={{width: "100%", display: "flex", justifyContent: "space-around"}}>
         {
           answer.map((elem, index) => (
             <button value={elem.answers} className="action-button animate blue welcome" key={index} onClick={(e) => {
               let findAnswer = answer.find(element => element.id == elem.id)
               
               if(findAnswer.isCorrent == true) {
                 handleOpen()
                }else {
                  handleOpenFirst()
                }
              }}>{elem.answers}</button>
              ))
            }
         </div>
         </div>
         </div>
            </div>
    </Wrapper>
  );
};

export default Select;
