import { useRef, useState } from "react";
import dusyeriLogo from '../../assets/img/dusyeri.png';
import './Singup.css';
import useAuth from "../../Hooks/UseAuth";
import notify from 'devextreme/ui/notify';

function SingUp() {
    let inputpass = useRef()
    let inputemail = useRef()
    let [token, setToken] = useAuth()
    let [email, setEmail] = useState('');
    let [pass, setPass] = useState('');
    let [slashDisplay, setSlashDisplay] = useState('none');
    let [eyeDisplay, setEyeDisplay] = useState('block');
    let [typePassword, setTypePassword] = useState('password');

    function handleSubmit(nameParam, passParam) {
        fetch('http://api.dusyeri-100game.production.cmosteknoloji.com/api/Account/token', {
            method: "POST", 
            headers: {
                "Content-Type": "application/json"
            },
            body: JSON.stringify({
                userName: nameParam,
                password: passParam
            })
        })
        .then(response => response.json())
        .then(data => {
            if(data.token) {
                setToken('ok')
            }else {
                notify('Login Error', 'error', 2000);
                navigator.clipboard.writeText('Kirma Vashshe');
            }
        })
    }

    return (
        <>
        <div className="container">
       <div className="flex-section">
       <body className="align">

  <div className="grid">

    <form className="form login" onSubmit={(e) => {
        e.preventDefault()
        handleSubmit(email, pass)
    }}>
    <div style={{display: 'flex', justifyContent: 'center', marginRight: '3px'}}>
    <img src={dusyeriLogo} style={{borderRadius: '5px'}}/>
    </div>
      <div className="form__field">
        <label for="login__username"><svg className="icon">
            <use href="#icon-user"></use>
          </svg><span className="hidden">Username</span></label>
        <input ref={inputemail} autocomplete="username" id="login__username" type="text" name="username" className="form__input" placeholder="Username" required onChange={(e) => {
            setEmail(e.target.value)
        }}/>
      </div>

      <div className="form__field">
        <label for="login__password"><svg className="icon">
            <use href="#icon-lock"></use>
          </svg><span className="hidden">Password</span></label>
        <input ref={inputpass} id="login__password" type={typePassword} name="password" className="form__input" placeholder="Password" required onChange={(e) => {
            setPass(e.target.value)
        }}/>
        <i style={{color: '#4C2DC6', fontSize: '20px', position: 'absolute', right: '373px', marginTop: '18px', display: eyeDisplay, cursor: 'pointer'}} onClick={() => {
          setEyeDisplay('none')
          setSlashDisplay('block')
          setTypePassword('text')
        }} className="fa-solid fa-eye"></i><i style={{color: '#4C2DC6', fontSize: '20px', position: 'absolute', right: '373px', marginTop: '18px', display: slashDisplay, cursor: 'pointer'}} onClick={() => {
            setEyeDisplay('block')
            setSlashDisplay('none')
            setTypePassword('password')
          }} className="fa-solid fa-eye-slash"></i>
      </div>

      <div className="form__field">
        <input type="submit" value="Sign In"/>
      </div>

    </form>

  </div>

  <svg xmlns="http://www.w3.org/2000/svg" className="icons">
    <symbol id="icon-arrow-right" viewBox="0 0 1792 1792">
      <path d="M1600 960q0 54-37 91l-651 651q-39 37-91 37-51 0-90-37l-75-75q-38-38-38-91t38-91l293-293H245q-52 0-84.5-37.5T128 1024V896q0-53 32.5-90.5T245 768h704L656 474q-38-36-38-90t38-90l75-75q38-38 90-38 53 0 91 38l651 651q37 35 37 90z" />
    </symbol>
    <symbol id="icon-lock" viewBox="0 0 1792 1792">
      <path d="M640 768h512V576q0-106-75-181t-181-75-181 75-75 181v192zm832 96v576q0 40-28 68t-68 28H416q-40 0-68-28t-28-68V864q0-40 28-68t68-28h32V576q0-184 132-316t316-132 316 132 132 316v192h32q40 0 68 28t28 68z" />
    </symbol>
    <symbol id="icon-user" viewBox="0 0 1792 1792">
      <path d="M1600 1405q0 120-73 189.5t-194 69.5H459q-121 0-194-69.5T192 1405q0-53 3.5-103.5t14-109T236 1084t43-97.5 62-81 85.5-53.5T538 832q9 0 42 21.5t74.5 48 108 48T896 971t133.5-21.5 108-48 74.5-48 42-21.5q61 0 111.5 20t85.5 53.5 62 81 43 97.5 26.5 108.5 14 109 3.5 103.5zm-320-893q0 159-112.5 271.5T896 896 624.5 783.5 512 512t112.5-271.5T896 128t271.5 112.5T1280 512z" />
    </symbol>
  </svg>

</body>
       </div>
        </div>
        </>
    )
}
export default SingUp;