import {
  Wrapper
} from "../../components";
import { useState, useRef } from "react";
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';
import './Label.css';

const LabelPage = () => {

  const [title, setTitle] = useState('')
  const [titleNth, setTitleNth] = useState('')

  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  const [openFirst, setOpenFirst] = useState(false);
  const handleOpenFirst = () => setOpenFirst(true);
  const handleCloseFirst = () => setOpenFirst(false);
  const [openLevel, setOpenLevel] = useState(false);
  const handleOpenLevel = () => setOpenLevel(true);
  const handleCloseLevel = () => setOpenLevel(false);
  const labelTitleRef = useRef()
  const labelPosterRef = useRef()

  let labelData = [
    {
      id: 1,
      labelPoster: "https://i.pinimg.com/originals/e0/b6/19/e0b619bb4def29cc007fba6c9b74cf99.jpg",
      labelTitle: "Lamborghini" 
    },
    {
      id: 2,
      labelPoster: "https://images2.alphacoders.com/501/501981.jpg",
      labelTitle: "Ferrari" 
    },
    {
      id: 3,
      labelPoster: "https://images4.alphacoders.com/105/1057695.jpg",
      labelTitle: "Bmw" 
    },
    {
      id: 4,
      labelPoster: "https://images5.alphacoders.com/103/1037674.jpg",
      labelTitle: "Merc" 
    },
    {
      id: 5,
      labelPoster: "https://images.hdqwalls.com/wallpapers/bugatti-chiron-new-6j.jpg",
      labelTitle: "Bugatti" 
    },
    {
      id: 6,
      labelPoster: "https://p4.wallpaperbetter.com/wallpaper/25/801/272/car-ford-mustang-street-wallpaper-preview.jpg",
      labelTitle: "Mustang" 
    },
    {
      id: 7,
      labelPoster: "https://images7.alphacoders.com/969/thumb-1920-969144.jpg",
      labelTitle: "Aston Martin" 
    },
    {
      id: 8,
      labelPoster: "https://pixelz.cc/wp-content/uploads/2018/08/porsche-911-turbo-s-exclusive-series-uhd-4k-wallpaper.jpg",
      labelTitle: "Porsche" 
    }
  ]

  const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 420,
    bgcolor: 'background.paper',
    border: '2px solid #000',
    boxShadow: 24,
    paddingTop: 8,
    paddingBottom: 5,
    paddingLeft: 5,
    paddingRight: 5,
  };

  return (
    <Wrapper>
      <div className="label-bg-section">
      <Modal
        open={openLevel}
        onClose={handleCloseLevel}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
        >
        <Box sx={style}>
  <div className="circle-wrapper">
    <div className="success circle"></div>
    <div className="icon">
      <i className="fa fa-check"></i>
    </div>
  </div>
          <Typography id="modal-modal-title" variant="h6" component="h2">
          Level success
          </Typography>
          <Typography id="modal-modal-description" sx={{ mt: 2 }}>
          Your Success level
          </Typography>
        </Box>
      </Modal>
        <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
        >
        <Box sx={style}>
  <div className="circle-wrapper">
    <div className="success circle"></div>
    <div className="icon">
      <i className="fa fa-check"></i>
    </div>
  </div>
          <Typography id="modal-modal-title" variant="h6" component="h2">
          Correct answer
          </Typography>
          <Typography id="modal-modal-description" sx={{ mt: 2 }}>
            Your answer is correct
          </Typography>
        </Box>
      </Modal>
      <Modal
        open={openFirst}
        onClose={handleCloseFirst}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
        >
        <Box sx={style}>
        <div className="circle-wrapper">
    <div className="error circle"></div>
    <div className="error-icon">
      <i className="fa fa-times"></i>
    </div>
  </div>
          <Typography id="modal-modal-title" variant="h6" component="h2">
          Wrong answer
          </Typography>
          <Typography id="modal-modal-description" sx={{ mt: 2 }}>
          Your answer is Wrong
          </Typography>
        </Box>
      </Modal>
      


           <div style={{display: 'flex', minHeight: '90vh', alignItems: 'center', width: '100%', justifyContent: 'center'}}>
           <div style={{display: 'flex', justifyContent: 'space-around', width: '40vh', flexWrap: 'wrap'}}>
           {
             labelData.map((elem, index) => (
               <button ref={labelTitleRef} key={index} style={{height: '10vh', marginTop: '15px'}} className="action-button-nth animate blue welcome" onClick={(e) => {
                 if(titleNth == '') {
                   setTitle(elem.id)
                  }else if(titleNth == elem.id) {
                    handleOpen()
                    setTitle('')    
                    setTitleNth('')
                  }else {
                    handleOpenFirst() 
                    setTitle('')
                    setTitleNth('')
                  }
              }}>{elem.labelTitle}</button> 
             ))
           }
           </div>
           <div style={{display: 'flex', justifyContent: 'space-between', flexWrap: 'wrap', width: '100vh', marginLeft: '100px', marginTop: '35px'}}>
           {
            labelData.map((element, index) => (
              <div key={index} style={{display: 'flex'}}>
                <img ref={labelPosterRef} style={{width: '250px', height: '200px', border: '3px solid #fff', marginLeft: '15px', borderRadius: '10px', cursor: 'pointer', marginBottom: '15px'}} src={element.labelPoster} alt="img" onClick={(e) => {
                  if(title == '') {
                    setTitleNth(element.id)
                  }else if(title == element.id) {
                    handleOpen()
                    setTitle('')
                    setTitleNth('')
                  }else {
                    handleOpenFirst() 
                    setTitle('')
                    setTitleNth('')
                  }
                }}/>
              </div>
            ))
           }
           </div>
           </div>
        </div>
    </Wrapper>
  );
};

export default LabelPage;
