import { useLocation, Route, Redirect } from 'react-router-dom'
import useAuth from '../../Hooks/UseAuth';

function Public(props){

    let [token] = useAuth()

    const {pathname} = useLocation()

    
    if(token == 'ok' && pathname == '/signup'){
        return <Redirect to="/"/>
    }
    return <Route {...props}/>
    
}

export default Public;