import {
  Wrapper
} from "../../components";
import { useState } from "react";
import "./Puzzle.css";
import { JigsawPuzzle } from "react-jigsaw-puzzle/lib";
import "react-jigsaw-puzzle/lib/jigsaw-puzzle.css";

const Puzzle = () => {
  const [text, setText] = useState("Unpuzzle the pieces!!");
      
    const set = () => {
        setText("Congratulations!!");
    };

  return (
    <Wrapper>
      <div className="puzzle-bg-section">
      <h2 className="tag">{text}</h2>
            <JigsawPuzzle
                imageSrc={'https://img.freepik.com/free-photo/cool-geometric-triangular-figure-neon-laser-light-great-backgrounds-wallpapers_181624-9331.jpg?w=2000'}
                rows={3}
                columns={3}
                onSolved={set}
                className="jigsaw-puzzle"
            />
      </div>
    </Wrapper>
  );
};

export default Puzzle;
