import {
  Wrapper
} from "../../components";
import { useState } from "react";
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';
import './Fixit.css';

const Fixit = () => {
  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  const [openFirst, setOpenFirst] = useState(false);
  const handleOpenFirst = () => setOpenFirst(true);
  const handleCloseFirst = () => setOpenFirst(false);
  const [textValue, setTextValue] = useState('')
  const [count, setCount] = useState(1)

 let finishData = textValue.split(' ').splice(1);
 let quizData = textValue.split(' ').splice(1).join(' ');

  let value = '';

  let textData = {
      text: "hello kutay what are you doing" 
  }

  let splitData = textData.text.split(' ');

let data =  splitData.sort(() => Math.random() - Math.random()).slice(0, splitData.length)

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  paddingTop: 8,
  paddingBottom: 5,
  paddingLeft: 5,
  paddingRight: 5,
};


  return (
    <Wrapper>
     <div className="fixit-big-background-section">
     <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
        >
        <Box sx={style}>
  <div className="circle-wrapper">
    <div className="success circle"></div>
    <div className="icon">
      <i className="fa fa-check"></i>
    </div>
  </div>
          <Typography id="modal-modal-title" variant="h6" component="h2">
          Level success
          </Typography>
          <Typography id="modal-modal-description" sx={{ mt: 2 }}>
          Your Success level
          </Typography>
        </Box>
      </Modal>
      <Modal
        open={openFirst}
        onClose={handleCloseFirst}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
        <div className="circle-wrapper">
    <div className="error circle"></div>
    <div className="error-icon">
      <i className="fa fa-times"></i>
    </div>
  </div>
          <Typography id="modal-modal-title" variant="h6" component="h2">
            Level failed
          </Typography>
          <Typography id="modal-modal-description" sx={{ mt: 2 }}>
            You failed level
          </Typography>
        </Box>
      </Modal>
      <div style={{display: "flex", justifyContent: "center", width: '100%', minHeight: '80vh', alignItems: 'center'}}>
      <div>
      <div style={{display: "block", marginBottom: "30px"}}>
      {
          data.map((elem, index) => (
              <button key={index} value={elem} className="action-button animate blue welcome" onClick={(e) => {
                  value = value + ' ' + e.target.value;

                  setTextValue(textValue + ' ' + e.target.value)
                  
                  if(textData.text.toLowerCase().trim().includes(quizData.toLowerCase().trim())) {    
                      setCount(count + 1)

                      console.log(count)
                      if(count >= splitData.length) {
                          handleOpen()
                        }
                    }else {
                        handleOpenFirst()
                    }
                }}>{elem}</button>
                
                ))
            }
      </div>
            <div style={{display: "flex", justifyContent: "center", width: "100%"}}>
              {
                finishData.map((elem, index) => (
                  <button value={elem} key={index} className="action-button animate blue welcome">{elem}</button>
                ))
              }
            </div>
      </div>
      </div>
     </div>
    </Wrapper>
  );
};

export default Fixit;
