import {
  Wrapper
} from "../../components";
import { useState } from "react";
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';
import './FindAmount.css';

const FindAmount = () => {
  const [openFirst, setOpenFirst] = useState(false);
  const handleOpenFirst = () => setOpenFirst(true);
  const handleCloseFirst = () => setOpenFirst(false);
  let [countTitle, setCountTitle] = useState()
  let [count, setCount] = useState(1)
  let [notCount, setNotCount] = useState(0)
  const [openLevel, setOpenLevel] = useState(false);
const handleOpenLevel = () => setOpenLevel(true);
const handleCloseLevel = () => setOpenLevel(false);

  let countData = [
      {
          id: 1,
          count: 6,
          icon: 'http://files.production.cmosteknoloji.com/6.svg'
      },
      {
          id: 2,
          count: 7,
          icon: 'http://files.production.cmosteknoloji.com/7.svg'
      },
      {
          id: 3,
          count: 5,
          icon: 'http://files.production.cmosteknoloji.com/5.svg'
      },
      {
          id: 4,
          count: 4,
          icon: 'http://files.production.cmosteknoloji.com/4.svg'
      }
  ]
  let notRandomCountData = [
      {
          id: 1,
          count: 6,
          icon: 'http://files.production.cmosteknoloji.com/6.svg'
      },
      {
          id: 2,
          count: 7,
          icon: 'http://files.production.cmosteknoloji.com/7.svg'
      },
      {
          id: 3,
          count: 5,
          icon: 'http://files.production.cmosteknoloji.com/5.svg'
      },
      {
          id: 4,
          count: 4,
          icon: 'http://files.production.cmosteknoloji.com/4.svg'
      }
  ]

  const style = {
      position: 'absolute',
      top: '50%',
      left: '50%',
      transform: 'translate(-50%, -50%)',
      width: 400,
      bgcolor: 'background.paper',
      border: '2px solid #000',
      boxShadow: 24,
      paddingTop: 8,
      paddingBottom: 5,
      paddingLeft: 5,
      paddingRight: 5,
    };

  let randomData =  countData.sort(() => Math.random() - Math.random()).slice(0, countData.length)
  return (
    <Wrapper>
      <div className="find-amount-section">
      <Modal
        open={openLevel}
        onClose={handleCloseLevel}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
        >
        <Box sx={style}>
  <div className="circle-wrapper">
    <div className="success circle"></div>
    <div className="icon">
      <i className="fa fa-check"></i>
    </div>
  </div>
          <Typography id="modal-modal-title" variant="h6" component="h2">
          Level success
          </Typography>
          <Typography id="modal-modal-description" sx={{ mt: 2 }}>
          Your Success level
          </Typography>
        </Box>
      </Modal>
      <Modal
        open={openFirst}
        onClose={handleCloseFirst}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
        <div className="circle-wrapper">
    <div className="error circle"></div>
    <div className="error-icon">
      <i className="fa fa-times"></i>
    </div>
  </div>
          <Typography id="modal-modal-title" variant="h6" component="h2">
            Level failed
          </Typography>
          <Typography id="modal-modal-description" sx={{ mt: 2 }}>
            You failed level
          </Typography>
        </Box>
      </Modal>
          <div style={{display: 'flex', justifyContent: 'center', width: '100%', alignItems: 'center', minHeight: '100vh'}}>
          <div>
          <div style={{marginBottom: '55px', display: 'flex', justifyContent: 'center'}}>
            {
                randomData.map((elem, index) => (
                    <button value={elem.count} key={index} style={{marginLeft: '15px'}} className="action-button animate blue welcome" type='button' onClick={(e) => {
                        setCountTitle(elem.count)
                    }}>{elem.count}</button>
                ))
            }
          </div>
          <div style={{display: 'flex', flexWrap: 'wrap', justifyContent: 'space-around'}}>
            {
                notRandomCountData.map((elem, index) => (
                    <div style={{marginLeft: '8px'}}>
                        <div>
                        <button value={elem.count} key={index} onClick={(e) => {
                          if(count >= notRandomCountData.length) {
                              handleOpenLevel()
                          }
                          if(notCount >= notRandomCountData.length / 2) {
                              handleOpenFirst()
                          }
                            if(countTitle == elem.count) {
                                e.target.textContent = elem.count;
                                setCount(count + 1)
                            }else {
                                setNotCount(notCount + 1)
                            }
                        }} style={{width: '100%', height: '55px'}} className="action-button animate blue welcome" type='button' onMouseEnter={(e) => {
                            if(!countTitle) {
                                e.target.style.cursor = 'no-drop'
                                e.target.disabled = true;
                            }else {
                                e.target.style.cursor = 'pointer';
                                e.target.disabled = false;
                            }
                        }}></button>
                    </div>
                        <div>
                    <img style={{width: '345px', height: '200px', border: '3px solid #FFF', borderRadius: '10px', marginBottom: '25px'}} key={index} src={elem.icon} alt='icon'></img>
                        </div>
                    </div>
                ))
            }
          </div>
          </div>
          </div>
      </div>
    </Wrapper>
  );
};

export default FindAmount;
