import {
  Wrapper
} from "../../components";
import './Match.css';
import { useState } from 'react';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';

const Match = () => {

  const [open, setOpen] = useState(false);
    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);
    const [openFirst, setOpenFirst] = useState(false);
    const handleOpenFirst = () => setOpenFirst(true);
    const handleCloseFirst = () => setOpenFirst(false);

    let answer = [
        {
            id: 1,
            answers: "anaconda",
            isCorrent: false
        },
        {
            id: 2,
            answers: "computer",
            isCorrent: false
        },
        {
            id: 3,
            answers: "code",
            isCorrent: false
        },
        {
            id: 4,
            answers: "python",
            isCorrent: true
        },
        {
            id: 5,
            answers: "animals",
            isCorrent: false
        },
        {
            id: 6,
            answers: "hacker",
            isCorrent: false
        },
        {
            id: 7,
            answers: "forest",
            isCorrent: false
        },
        {
            id: 8,
            answers: "intelligence",
            isCorrent: false
        }
    ]

    
    let quest = [
      {questPoster: "https://wallpaperaccess.com/full/3262691.jpg"},
      {questPoster: "https://images7.alphacoders.com/122/1228014.png"},
      {questPoster: "https://admincp.semicolon-ltd.com/images/article/24/Human-Intelligence-Can-Fix-AI-Shortcomings-1.jpg"}
    ]
    const style = {
      position: 'absolute',
      top: '50%',
      left: '50%',
      transform: 'translate(-50%, -50%)',
      width: 400,
      bgcolor: 'background.paper',
      border: '2px solid #000',
      boxShadow: 24,
      paddingTop: 8,
      paddingBottom: 5,
      paddingLeft: 5,
      paddingRight: 5,
    };

  return (
    <Wrapper>
      <div className="match-bg-section">
        <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
        >
        <Box sx={style}>
  <div className="circle-wrapper">
    <div className="success circle"></div>
    <div className="icon">
      <i className="fa fa-check"></i>
    </div>
  </div>
          <Typography id="modal-modal-title" variant="h6" component="h2">
            Level success
          </Typography>
          <Typography id="modal-modal-description" sx={{ mt: 2 }}>
            You succeeded level
          </Typography>
        </Box>
      </Modal>
      <Modal
        open={openFirst}
        onClose={handleCloseFirst}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
        >
        <Box sx={style}>
        <div className="circle-wrapper">
    <div className="error circle"></div>
    <div className="error-icon">
      <i className="fa fa-times"></i>
    </div>
  </div>
          <Typography id="modal-modal-title" variant="h6" component="h2">
            Level failed
          </Typography>
          <Typography id="modal-modal-description" sx={{ mt: 2 }}>
            You failed level
          </Typography>
        </Box>
      </Modal>
         <div>
         <div style={{width: "100%", display: "flex", justifyContent: "space-around", flexWrap: "wrap", minHeight: '70vh', alignItems: 'center'}}>
         {
           quest.map((elem, index) => (
             <img key={index} style={{width: "22%", borderRadius: "10px", border: "3px solid #fff"}} src={elem.questPoster} alt="img"/>
             ))
            }
         </div>
         <div style={{display: "flex", justifyContent: "center", marginTop: '-100px'}}>
         <div style={{width: "80%", display: "flex", justifyContent: "space-around", minHeight: '20vh', flexWrap: "wrap"}}>
         {
           answer.map((elem, index) => (
             <button value={elem.answers} className="action-button animate blue welcome" style={{marginBottom: "15px", marginLeft: '10px'}} key={index} onClick={(e) => {
               let findAnswer = answer.find(element => element.id == elem.id)
               
               if(findAnswer.isCorrent == true) {
                 handleOpen()
                }else {
                  handleOpenFirst()
                }
              }}>{elem.answers}</button>
              ))
              }
         </div>
         </div>
         </div>
              </div>
    </Wrapper>
  );
};

export default Match;
