import {
  Wrapper
} from "../../components";
import './Test.css';
import ReactAudioPlayer from 'react-audio-player';
import Button from '@mui/material/Button';
import Stack from '@mui/material/Stack';
import { styled } from '@mui/material/styles';

const Test = () => {
  let quest = {
    questions: "kutay who are you ?"
};
const Input = styled('input')({
  display: 'none',
});
  return (
    <Wrapper>
      <div className='big-bg-section'>
           <div style={{display: 'flex', width: '100%', minHeight: '100vh', justifyContent: 'center', alignItems: 'center'}}>
           <form onSubmit={(e) => {
            e.preventDefault()
           }}>
           <div style={{display: "flex", width: "100%", justifyContent: "center"}}><button type='button' className="action-button animate blue welcome">
<ReactAudioPlayer
  src="https://www.soundhelix.com/examples/mp3/SoundHelix-Song-1.mp3"
  controls
/>        
            <br/> {quest.questions}</button></div>
           <input style={{marginTop: "30px"}} className='select-big-input' type='text' placeholder='answer' required/>
           <Stack style={{marginLeft: '10px', marginTop: '15px'}}>
      <label htmlFor="contained-button-image">
        <Input accept="image/*" id="contained-button-image" multiple type="file" required/>
        <Button style={{width: '50vh', height: '66px'}} variant="contained" className='action-button animate blue welcome' component="span">
          Upload Image
        </Button>
      </label>
      <label htmlFor="icon-button-image">
        <Input accept="image/*" id="icon-button-image" type="file" required/>
      </label>
    </Stack>
    <Stack style={{marginLeft: '10px', marginTop: '15px'}}>
      <label htmlFor="contained-button-video">
        <Input accept=".mp4" id="contained-button-video" multiple type="file" required/>
        <Button style={{width: '50vh', height: '66px'}} variant="contained" className='action-button animate blue welcome' component="span">
          Upload Video
        </Button>
      </label>
      <label htmlFor="icon-button-video">
        <Input accept=".mp4" id="icon-button-video" type="file" required/>
      </label>
    </Stack>
    <Stack style={{marginLeft: '10px', marginTop: '15px'}}>
      <label htmlFor="contained-button-audio">
        <Input accept=".mp3" id="contained-button-audio" multiple type="file" required/>
        <Button style={{width: '50vh', height: '66px'}} variant="contained" className='action-button animate blue welcome' component="span">
          Upload Audio
        </Button>
      </label>
      <label htmlFor="icon-button-audio">
        <Input accept=".mp3" id="icon-button-audio" type="file" required/>
      </label>
    </Stack>
    <div style={{display: 'flex', justifyContent: 'flex-end', marginTop: '10px'}}><button type='submit' className="action-button animate blue welcome">Submit</button></div>
           </form>
           </div>
           </div> 
    </Wrapper>
  );
};

export default Test;
